import { request, requestDownload } from 'utils';

const prefix = '/api/organization';
export const getUser = () =>
  request({
    prefix: '/identity',
    url: `/sign-in`,
    data: { username: 'admin', password: 'admin' },
    method: 'POST',
  });

export const getDocOut = paramsSearch =>
  request({
    prefix,
    url: `/DocOut`,
    params: { ...paramsSearch },
    method: 'GET',
  });

export const createDocOut = (newDoc, token, headers = {}) =>
  request({
    prefix,
    url: `/DocOut`,
    headers: {
      Accept: 'application/json',
      'Content-Type': 'application/json',
      Authorization: `Bearer ${token}`,
      ...headers,
    },
    data: { ...newDoc },
    method: 'POST',
  });

export const deleteDocOut = (id, token, headers = {}) =>
  request({
    prefix,
    url: `/DocOut`,
    params: { id },
    headers: {
      Accept: 'application/json',
      'Content-Type': 'application/json',
      Authorization: `Bearer ${token}`,
      ...headers,
    },
    method: 'DELETE',
  });

export const getDocOutId = id =>
  request({
    prefix,
    url: `/DocOut/GetById`,
    params: { id },
    method: 'GET',
  });

export const updateDocOut = (editDoc, token, headers = {}) =>
  request({
    prefix,
    url: `/DocOut`,
    data: { ...editDoc },
    headers: {
      Accept: 'application/json',
      'Content-Type': 'application/json',
      Authorization: `Bearer ${token}`,
      ...headers,
    },
    method: 'PUT',
  });

export const downloadFile = (paramsSearch, token, headers = {}) =>
  requestDownload({
    prefix,
    defaultFileName: 'ExportDocOuts.xlsx',
    url: `/DocOut/ExportDocOuts`,
    data: { ...paramsSearch },
    headers: {
      Accept: 'application/json',
      'Content-Type': 'application/json',
      Authorization: `Bearer ${token}`,
      ...headers,
    },
    method: 'POST',
  });



